import request from '../../api/request.js'
import global from '../../static/js/global.js'
export default {
	data() {
		return {
			total:'',
			refresh:false,
			source: 0,
			curMonth:'',
			curPage:1,
			offset:null,
			limit:10,
			addressList: [
				
			],
			tabList:[
				{label:'全部',value:''},
				{label:'3个月内',value:3},
				{label:'半年内',value:6}
			]
		}
	},
	onShow(){
		if(this.refresh){
			this.refresh=false
			this.getList()
			// this.source = option.source;
		}
	},
	onLoad(option){
		this.getList()
		// this.source = option.source;
	},
	methods: {
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pages/hidTrouble/hidTroubleEdit`
			})
		},
		gorepair(item){
			// uni.navigateTo({
			// 	url:"/pages/repair/repairEdit?item="+JSON.stringify(item)
			// })
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
		},
		//获取报修列表
		getList(){
			if(this.total!='' && this.total<=this.addressList.length){
				return
			}
			var that=this
			global.showLoading()
			var param = {
				openId:'wx123456789',
				month:this.curMonth,
				page:this.curPage,
				limit:this.limit
			}
			request.apiGet('/toc/deviceRepair/listRepair',param).then((res) =>{
				if(res.code == '0'){
					that.curPage++
					var total=[]
					that.total=res.total
					total=res.data
					// res.data.forEach((item,index) =>{
					// 	total.push({
					// 		name:item.devName,
					// 		user: item.contact,
					// 		time: item.updateTime,
					// 		default: true
					// 	})
					// })
					that.addressList=that.addressList.concat(total)
					global.hideLoading()
				}else{
					global.hideLoading()
					global.showToast(res.msg)
				}
			}).catch((reason) =>{
				global.hideLoading()
				global.showToast(reason)
			})
		},
		tap(v){
			this.curMonth=v
			this.curPage=1
			this.limit=10,
			this.total=''
			this.addressList=[]
			this.getList()
		},
		lower(){
			this.getList()
		}
	}
}
